import { AotSummaryResolver } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import * as AOS from 'aos';
import Typewriter from 't-writer.js';
import * as $ from 'jquery';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'kunal-portfolio';
  visiterCounter: string = "Yallaling";

  ngOnInit()
  {
    var counter: any;
   counter = Number(localStorage.getItem('visiter_count'));
    //console.log(counter);
      counter = counter + 1;
      localStorage.setItem('visiter_count',counter);
      this.visiterCounter = counter;
      //console.log(localStorage.getItem('visiter_count'));
      
      
    
    
    AOS.init();
    const target = document.querySelector('.home__title');

    const writer = new Typewriter(target, {
      loop: true, 
      typeColor: 'black' 
    })
    
    writer
      .strings(
        400,
        "Software Developer",
        "RPA Developer", 
        "Full Stack Developer",
        "Machine Learning Enthusiast",
      )
      .start()
      
  }
}
